
*{
  padding: 0;
  margin: 0;
  text-decoration: none;
  list-style: none;
  box-sizing: border-box;
}
nav {
  background-color:  #19334d;
  height: 100px;
  width: 100%;
  z-index: 4;
}
.title
{
  color: white;
  line-height: 90px;
  padding:0 50px;
  font-weight:bold;
  font-size: 50px;
}
nav ul
{
  display:flex;
  flex-direction: row-reverse;
  margin-top: -60px;
}
nav ul li
{
  display: inline-block;
  line-height: 50px;
  margin-right: 20px;
}
nav ul li .list{
  color: white;
  font-size:15px;
  padding: 7px 13px;
  border-radius: 3px;
  font-weight: bold;
}
.active,.list:hover{
  background:white;
  color:black;
  transition: .5s;
  cursor:pointer;
}
.menu
{
  color:white;
  font-size:30px;
  float:right;
  line-height: 90px;
  margin-right: 30px;
  cursor: pointer;
  display: none;
}
#check{
  display: none;
}
​.dropdown {
  position: relative;
  display: inline-block;
}
.dropdown-content {
  display: none;
  position: absolute;
}
.dropdown-content a{
  display: block;
  padding: 10px;
  color: white;
  margin-bottom: -25px;
  background-color: #19334d;
  font-weight: bold;
  z-index: 1;
}
Link{
  display: none;
}
.dis{
  display: none;
}
#key{
  display: block;
  color: white;
}
.dropdown:hover .dropdown-content {
  display: block;
}
.dropdown-content a:hover {
  background-color: white;
  color: black;
  cursor: pointer;
}
@media (max-width:850px){
  .menu
  {
    display: block;
  }
  ul{
    position: fixed;
    width: 100%;
    height: 350px;
    background:rgba(102, 140, 255,0.5) ;
    top:160px;
    left:-100%;
    text-align: center;
    transition: all .5s;
  }
  nav ul
  {
    display:block;
  }
  nav ul li
  {
    display: block;
    margin: 50px 0;
    line-height: 30px;
  }
  nav ul li .list{
    font-size: 20px;
    font-weight: bold;
  }
  .list:hover,.active{
    background: none;
    color:black;
  }
  #check:checked ~ ul{
    left:0;
  }
  ​.dropdown {
    display: inline-block;
  }
  .dropdown-content {
    display: none;
    width: 100%;
    margin-top: -50px;
    background:rgb(102, 140, 255) ;
    text-align: center;
    z-index: 1;
  }
}
img{
  width: 100%;
  height: auto;
}
